<template>
  <div class="container" v-loading.fullscreen.lock="loading">
    <!-- title -->
    <!-- <page-title first-title="" second-title=""></page-title> -->
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- content -->
    <div class="good-info-wrapper">
      <el-form label-width="150px" :rules="goodRules" :model="good_infos" ref="goodForm">
        <el-form-item label="" prop="goods_name">
          <el-input :disabled="notEdit" v-model="good_infos.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <!-- <el-input :disabled="notEdit" v-model="good_infos.goods_number"></el-input> -->
          <el-input :disabled="notEdit" v-model="remain_goods_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="secbuy_quantity">
          <el-input v-model="good_infos.secbuy_quantity" clearable></el-input>
          <span>()</span>
        </el-form-item>
        <el-form-item label="" prop="secbuy_close_price">
          <el-input v-model="good_infos.secbuy_close_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="settlement_surplus">
          <el-input v-model="good_infos.settlement_surplus"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_shop_price">
          <el-input :disabled="notEdit" v-model="good_infos.goods_shop_price"></el-input>
          <div class="price-wrapper">
            <p>
              <label></label>
              <el-input :disabled="notEdit" v-model="good_infos.goods_cash"></el-input>
            </p>
            <p>
              <label></label>
              <el-input :disabled="notEdit" v-model="good_infos.goods_money_line"></el-input>
            </p>
          </div>
        </el-form-item>
        <el-form-item label="" prop="shop_price">
          <template v-if="good_infos.secbuy_type == 2">
            <el-input :disabled="notEdit" v-model="total_cash_price" clearable></el-input>
            <span>: {{ currentDiscount }}</span>
            <div class="price-wrapper">
              <p>
                <label></label>
                <el-input v-model="good_infos.cash_price" clearable></el-input>
              </p>
              <p>
                <!-- <el-form-item label="">
                  <el-input v-model="good_infos.huanbi_price" clearable></el-input>
                </el-form-item> -->
                <label></label>
                <el-input v-model="good_infos.huanbi_price" clearable></el-input>
              </p>
            </div>
          </template>
          <template v-else>
            <el-input :disabled="notEdit" v-model="good_infos.shop_price" clearable></el-input>
            <div class="price-wrapper">
              <p>
                <label></label>
                <el-input v-model="good_infos.cash_price" clearable></el-input>
              </p>
              <p>
                <label></label>
                <el-input v-model="good_infos.huanbi_price" clearable></el-input>
              </p>
            </div>
          </template>
        </el-form-item>
        <el-form-item label="" prop="max_quantity">
          <p class="radio-wrapper-p"><el-radio v-model="limit_buy" label="1"></el-radio></p>
          <p class="radio-wrapper-p">
            <el-radio v-model="limit_buy" label="2"></el-radio
            ><el-input v-model="good_infos.max_quantity" :disabled="limit_buy != '2'"></el-input>
          </p>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveGood"></el-button>
          <el-button @click="goBack"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import { SaveGoodsInfo, GetGoods } from '../../api/secondkill'
// import { PAYTYPE } from './static'
export default {
  name: 'KillGoodEdit',
  data() {
    // 
    // 
    let vSecbuyQuantity = (rule, value, callback) => {
      if (value) {
        // if (Number(value) > Number(this.good_infos.goods_number)) {
        if (Number(value) > this.total_goods_number) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
    }
    // 
    let vMaxQuantity = (rule, value, callback) => {
      if (this.good_infos.secbuy_quantity && Number(value) > Number(this.good_infos.secbuy_quantity)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    /**
     * secbuy_type: 1 2
     * rule: type1 = 
     * type2
     * goods_shop_price  goods_cash,  goods_money_line
     *  shop_price   cash_price  huanbi_price
     */
    let vCash = (rule, value, callback) => {
      if (this.good_infos.secbuy_type == 2) {
        if (Number(value) >= Number(this.good_infos.goods_shop_price)) {
          callback(new Error(''))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
    // 
    let vNumber = (rule, value, callback) => {
      if (value) {
        if (isNaN(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
    }
    return {
      // paytype: PAYTYPE,
      notEdit: true, // 
      good_infos: {},
      goodRules: {
        secbuy_close_price: [
          { required: true, message: '', trigger: 'blur' },
          { validator: vNumber, message: '', trigger: 'blur' }
        ],
        shop_price: [
          { required: true, message: '', trigger: 'blur' },
          { validator: vNumber, message: '', trigger: 'blur' },
          { validator: vCash, trigger: 'blur' }
        ],
        secbuy_quantity: [
          { required: true, message: '', trigger: 'blur' },
          { validator: vSecbuyQuantity, trigger: 'blur' },
          { validator: vNumber, message: '', trigger: 'blur' }
        ],
        max_quantity: [
          { required: true, message: '', trigger: 'blur' },
          { validator: vMaxQuantity, trigger: 'blur' },
          { validator: vNumber, message: '', trigger: 'blur' }
        ],
        goods_order: [
          { required: true, message: '', trigger: 'blur' },
          { validator: vNumber, message: '', trigger: 'blur' }
        ]
      },
      loading: false,
      limit_buy: '1',
      total_goods_number: undefined // 
    }
  },
  computed: {
    // 
    total_cash_price() {
      this.good_infos.shop_price = Number(this.good_infos.huanbi_price) + Number(this.good_infos.cash_price)
      return this.good_infos.shop_price
    },
    current_cash_price() {
      return this.good_infos.cash_price
    },
    current_huanbi_price() {
      return this.good_infos.huanbi_price
    },
    remain_goods_number() {
      return this.total_goods_number - Number(this.good_infos.secbuy_quantity)
    },
    currentDiscount() {
      return ((Number(this.good_infos.shop_price) * 10) / Number(this.good_infos.goods_shop_price)).toFixed(1)
    }
  },
  watch: {
    current_cash_price() {
      if (this.good_infos.secbuy_type == 1) {
        this.good_infos.huanbi_price = Number(this.good_infos.shop_price) - Number(this.good_infos.cash_price)
      }
    },
    current_huanbi_price() {
      if (this.good_infos.secbuy_type == 1) {
        this.good_infos.cash_price = Number(this.good_infos.shop_price) - Number(this.good_infos.huanbi_price)
      }
    }
  },
  created() {
    this.loading = true
    GetGoods(this.$route.params.goodID).then(res => {
      this.good_infos = res[0]
      this.total_goods_number = Number(this.good_infos.goods_number) + Number(this.good_infos.secbuy_quantity)
      this.limit_buy = this.good_infos.max_quantity > 0 ? '2' : '1'
      this.loading = false
    })
  },
  methods: {
    saveGood() {
      this.$refs['goodForm'].validate(valid => {
        if (valid) {
          /*if (
            Number(this.good_infos.shop_price) !==
            Number(this.good_infos.cash_price) + Number(this.good_infos.huanbi_price)
          ) {
            this.$message.error('')
            return
          }*/
          if (this.good_infos.secbuy_type == 1) {
            if (this.good_infos.huanbi_price <= this.good_infos.goods_money_line) {
              this.$message.error('')
              return
            }
          }
          // 0
          if (this.limit_buy == '1') {
            this.good_infos.max_quantity = 0
          }
          SaveGoodsInfo(this.good_infos).then(res => {
            if (res) {
              this.$message.success('')
              this.goBack()
            }
          })
        }
      })
    },
    goBack() {
      // replacewindows.history
      this.$router.replace({ name: 'PromoteSaleAdd', query: { id: this.$route.params.actID } })
    }
  }
}
</script>
<style lang="scss" scoped>
.good-info-wrapper {
  margin-top: 15px;
  .radio-wrapper-p {
    margin: 0;
  }
  .el-input {
    width: 220px;
  }

  .price-wrapper {
    p {
      width: 220px;
      display: flex;

      label {
        width: 150px;
      }
    }
  }

  span {
    margin-left: 20px;
    color: #999;
  }
}
</style>
